import http from "http";

export class NanoResponse {
    public httpResponse: http.ServerResponse;

    constructor(httpResponse: http.ServerResponse) {
        this.httpResponse = httpResponse;
    }

    public writeResponse(message: string | Buffer | Uint8Array, contentType = "text/html; charset=utf-8", statusCode: number = 200) {
        this.httpResponse.writeHead(statusCode, { 'Content-Type': contentType });
        if (typeof message == "string") {
            this.httpResponse.end(message, "utf-8");
        } else {
            this.httpResponse.end(message);
        }
    }

    public writeHeaders(headers?: Record<string, string | Array<string>>){
        if (headers) {
            Object.keys(headers).forEach(header => this.httpResponse.setHeader(header, headers[header]));
        }
    }
};